import React, { useState, useEffect } from 'react'
import './Home.css'
import Footer from './Footer';
import { useDispatch } from 'react-redux';
import { adduser } from './redux/slice';
import { Carousel } from 'antd'
import { Link } from 'react-router-dom';

const banner = [
    {
        img: 'https://themes12.anvanto.com/super/themes/birdwings/modules/an_homeslider/img/a943a7a903d8106fdf0fc03b443cf064_1.jpg',
        label: 'Hanging',
        label2: 'Chairs',
        para: 'lorem ipsum dolor sit amet consectetur',
        btn: 'SHOP NOW',
        link: '#'
    },
    {
        img: 'https://themes12.anvanto.com/super/themes/birdwings/modules/an_homeslider/img/9bfbcf844c962980e1476d22e5c7a1b1_1.jpg',
        label: 'Hanging',
        label2: 'Chairs',
        para: 'lorem ipsum dolor sit amet consectetur',
        btn: 'SHOP NOW',
        link: '#'
    },
    {
        img: 'https://themes12.anvanto.com/super/themes/birdwings/modules/an_homeslider/img/7779d97876d0544fa6821156f45197a9_1.jpg',
        label: 'Hanging',
        label2: 'Chairs',
        para: 'lorem ipsum dolor sit amet consectetur',
        btn: 'SHOP NOW',
        link: '#'
    }
]

const Home = () => {

    var baseurl = process.env.REACT_APP_URL
    const dispatch = useDispatch();

    const [data, setdata] = useState('');

    const result = async () => {
        let data = await fetch('https://ecomnode.vercel.app/product', {
            headers: {
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        })
        data = await data.json();
        setdata(data);
    }
    useEffect(() => {
        result();
    }, [])

    const order = async (name, price, img, color, categorie) => {
        let data = await fetch('https://ecomnode.vercel.app/order', {
            method: 'post',
            body: JSON.stringify({ name, price, img, color, categorie }),
            headers: {
                'Content-Type': 'application/json',
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        })
        data = await data.json();
        if (data.status) {
            alert(`${name} order`);
        } else {
            alert(`${name} order failed`);
        }

        dispatch(adduser({ name, price, img, color, categorie }));
    }

    return (
        <div>
            <div className='carousel'>
                <Carousel
                    autoplay
                    dots={true}
                    pauseOnDotsHover={true}
                    pauseOnHover={true}
                    draggable
                >
                    {
                        banner.map((item, ind) => {
                            return (
                                <div>
                                    <div className='carousel-data'>
                                        <h1 className='carousel-heading'>{item.label}<br />{item.label2}</h1>
                                        <p className='carousel-para'>{item.para}</p>
                                        <a className='carousel-btn' href={item.link}>{item.btn}</a>
                                    </div>
                                    <img src={item.img} className='carousel-img' alt='B1' />
                                </div>
                            )
                        })
                    }
                </Carousel>
            </div>

                <h1 className='we-recommended'>New arrivals</h1>
                <div className='homeclient'>
                    {
                        data.length ?
                            data.map((item, ind) => {
                                return (
                                    <div key={item._id} className='product-list'>
                                        <img className='product-img' src={item.img} alt='img...' />
                                        <h3 className='product-names'>{item.name}</h3>
                                        <span className='product-price'>€{item.price}</span>
                                        {
                                            <div className='mobile-size-color'>
                                                <select className='product-size'>
                                                    <option>40 x 60</option>
                                                    <option>80 x 120</option>
                                                </select>
                                            </div>
                                        }
                                        <input onClick={() => order(item.name, item.price, item.img, item.color, item.categorie)} className='stock' type='button' value='ADD TO CART' style={{cursor: 'pointer'}} />
                                    </div>
                                )
                            }) : null
                    }
                </div>

            {/* new colloction */}

            <div className='colloction'>
                <div className='colloction-one'>
                    <div className='colloction-img'>
                        <img className='colloction-imgs' src='https://themes12.anvanto.com/super/themes/birdwings/modules/an_homeproducts/img/217a0c3c474d30fb5ab010575f29c119_1.jpg' alt='...' />
                    </div>
                    <div className='colloction-detail'>
                        <h6 style={{ color: '#000', fontSize: '14px' }}>New Colloction</h6>
                        <h3 className='colorful'>Colorful poufs for your comfort!</h3>
                        <span className='discover'>DISCOVER</span>
                    </div>
                </div>

                <div className='colloction-two'>
                    <div>
                        <img className='colloction-imgs' src='https://themes12.anvanto.com/super/themes/birdwings/modules/an_homeproducts/img/bfdaf9b8ce2683c1ac5550fd6fb8765d_1.jpg' alt='...' />
                    </div>
                    <div className='colloction-details'>
                        <h6>HUGE SELECTION OF POTS</h6>
                        <h6>DISCOUNT <span style={{ color: 'orange' }}>$5.00</span></h6>
                    </div>
                </div>
            </div>

                <h1 className='we-recommended'>We Recommended</h1>
                <div className='homeclient'>
                    {
                        data.length ?
                            data.map((item, ind) => {
                                return (
                                    <div key={item._id} className='product-list'>
                                        <img className='product-img' src={item.img} alt='img...' />
                                        <h3 className='product-names'>{item.name}</h3>
                                        <span className='product-price'>€{item.price}</span>
                                        {
                                            <div className='mobile-size-color'>
                                                <select className='product-size'>
                                                    <option>40 x 60</option>
                                                    <option>80 x 120</option>
                                                </select>
                                            </div>
                                        }
                                        <input onClick={() => order(item.name, item.price, item.img, item.color, item.categorie)} className='stock' type='button' value='ADD TO CART' style={{cursor: 'pointer'}} />
                                    </div>
                                )
                            }) : null
                    }
                </div>

            <Footer />
        </div>
    )
}

export default Home;
