import React, { useState, useEffect } from 'react'
import './Home.css'
import Footer from './Footer'
import { useDispatch } from 'react-redux';
import { adduser } from './redux/slice';

const Allproduct = () => {

  var baseurl = process.env.REACT_APP_URL
  const dispatch = useDispatch();

  const [unique, setunique] = useState([]);
  const [data, setdata] = useState('');

    const result = async () => {
        let data = await fetch('https://ecomnode.vercel.app/product', {
            headers: {
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        })
        data = await data.json();
        setunique(data);
    }
    useEffect(() => {
        result();
    }, [])

  const order = async (name, price, img, color, categorie) => {
    let data = await fetch('https://ecomnode.vercel.app/order', {
      method: 'post',
      body: JSON.stringify({ name, price, img, color, categorie }),
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    if (data.status) {
      alert(`${name} order`);
    } else {
      alert(`${name} order failed`);
    }

    dispatch(adduser({ name, price, img, color, categorie }));

  }

  return (
    <div>
      <div>
        <h1 style={{marginTop: '15px'}} className='we-recommended'>New arrivals</h1>
        <div className='homeclient'>
          {
            unique.length ?
              unique.map((item, ind) => {
                return (
                  <div key={item._id} className='product-list'>
                    <img className='product-img' src={item.img} alt='img...' />
                    <h3 className='product-names'>{item.name}</h3>
                    <span className='product-price'>€{item.price}</span>
                    {
                      <div className='mobile-size-color'>
                        <select className='product-size'>
                          <option>40 x 60</option>
                          <option>80 x 120</option>
                        </select>
                      </div>
                    }
                    <input onClick={() => order(item.name, item.price, item.img, item.color, item.categorie)} className='stock' type='button' value='ADD TO CART' style={{cursor: 'pointer'}} />
                  </div>
                )
              }) : null
          }
        </div>
      </div>

      <Footer />
    </div>
  )
}

export default Allproduct

