import React from 'react'
import './Login.css'
import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

const Singup = () => {

    var baseurl = process.env.REACT_APP_URL
    const navigate = useNavigate();

    const [user, setuser] = useState();
    const [email, setemail] = useState();
    const [password, setpassword] = useState();

    const singup = async () => {
        let data = await fetch('https://ecomnode.vercel.app/user', {
            method: "post",
            body: JSON.stringify({ user, email, password }),
            cache: "no-cache",
            headers: {
                'Content-Type': 'application/json',
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        });
        data = await data.json();
        let status = data.status;
        if (status) {
            localStorage.setItem('user', JSON.stringify(data.data))
            if (localStorage.getItem('user')) {
                navigate('/');
            }
        } else {
            alert('something was wrong pls check');
        }
    }

    return (
        <div>
            <div className='login'>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon1">
                        person
                    </span>
                    <input type="text" className="form-control" value={user} onChange={(e) => setuser(e.target.value)} placeholder="Username" aria-label="Username" aria-describedby="basic-addon1" />
                </div>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon2">
                        mail
                    </span>
                    <input type="email" className="form-control" value={email} onChange={(e) => setemail(e.target.value)} placeholder="Email" aria-label="Email" aria-describedby="basic-addon2" />
                </div>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon3">
                        key
                    </span>
                    <input type="password" className="form-control" value={password} onChange={(e) => setpassword(e.target.value)} placeholder="Password" aria-label="Password" aria-describedby="basic-addon3" />
                </div>
                <button onClick={singup} className='btn btn-dark btn-login'>Singup</button>
                <p className='acc'>Already have an account <Link to='/login'>Login</Link></p>
            </div>
        </div>
    )
}

export default Singup;