import React, { useEffect, useState } from 'react'
import './Categorie.css'

const Categories = () => {

  var baseurl = process.env.REACT_APP_URL
  const [lists, setlists] = useState('');

  const [cate, setcate] = useState('');

  const list = async () => {
    let result = await fetch(`https://ecomnode.vercel.app/categorie`,{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    result = await result.json();
    setlists(result);
  }

  useEffect(() => {
    list();
  })

  const save = async () => {
    let result = await fetch(`https://ecomnode.vercel.app/categorie`, {
      method: 'post',
      body: JSON.stringify({ cate }),
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    result = await result.json();
    if (result.status) {
      alert('SUCCESS: Data inserted On database seccessfully !!!');
      if (alert) {
        window.location.reload();
      }
    } else {
      alert('ERROR: error inserting data !!!')
    }
  }

  const deleted = async (ele) => {
    let data = await fetch(`https://ecomnode.vercel.app/categorie/${ele}`, {
      method: 'delete',
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    console.log(data);
    if (data.status) {
      alert('SUCCESS: data deleted SUCCESSFULLY !!!')
      if (alert) {
        window.location.reload();
      }
    } else {
      alert('ERROR: data not deleted')
    }
  }

  return (
    <div>
      <h4 className='cate'>Categories</h4>
      <p className='create'>Create new category</p>
      <input className='input' type='text' value={cate} onChange={(e) => setcate(e.target.value)} placeholder='Category name' /><br />
      <button onClick={save} className='save-btn'>Save</button>
      <p className='cate-name'>CATEGORY NAME</p>
      <div className='lists'>
        {
          lists.length ?
            lists.map((ele, ind) => {
              return (
                <div className='data-list'>
                  <h6 className='cate-list-name'>{ele.cate}</h6>
                  <button className='btn-del'>
                    <span className="material-symbols-outlined">
                      delete
                    </span>
                    <button onClick={() => deleted(ele._id)} className='del'>Delete</button>
                  </button>
                </div>
              )
            }) : null
        }
      </div>
    </div>
  )
}

export default Categories
