import React, { useEffect, useState } from 'react'
import './Dashboard.css'

const Dashboard = () => {

  var baseurl = process.env.REACT_APP_URL

  const [revenue, setrevenue] = useState([])
  const [sell, setsell] = useState(0);

  const [admins, setadmins] = useState('');
  const [users, setusers] = useState('');
  const [orders, setorders] = useState('');

  const admin = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/admin`,{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    setadmins(data.length);
  }

  const user = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/user`,{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    setusers(data.length);
  }

  const order = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/order`,{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    setrevenue(data);
    setorders(data.length);
  }

  useEffect(()=>{
    const sum = revenue.reduce((acc, data) => acc + parseFloat(data.price), 0);
    setsell(sum); 
  }, [revenue])

  useEffect(()=>{
    admin();
    user();
    order();
  }, [])

  return (
    <div >
      <p className='data'>Basic-details</p>
      <div className='datas'>
        <div className='data-all'>
          <p className='data-name'>Admin</p>
          <h1 className='data-no'>{admins}</h1>
          <p className='data-write'>Total admin {admins}</p>
        </div>

        <div className='data-all'>
          <p className='data-name'>User</p>
          <h1 className='data-no'>{users}</h1>
          <p className='data-write'>Total users {users}</p>
        </div>

        <div className='data-all'>
          <p className='data-name'>Order</p>
          <h1 className='data-no'>{orders}</h1>
          <p className='data-write'>Total order {orders}</p>
        </div>
      </div>

      <p className='data'>Revenue</p>
      <div className='datas'>
        <div className='data-all'>
          <p className='data-name'>Sell</p>
          <h1 className='data-no'>₹ {sell}</h1>
          <p className='data-write'>Total sell ₹ {sell}</p>
        </div>

        <div className='data-all'>
          <p className='data-name'>Margin</p>
          <h1 className='data-no'>₹ {sell-81*sell/100}</h1>
          <p className='data-write'>Total margin ₹ {sell-81*sell/100}</p>
        </div>

        <div className='data-all'>
          <p className='data-name'>Investment</p>
          <h1 className='data-no'>₹ {sell- (sell-81*sell/100)}</h1>
          <p className='data-write'> Total invest ₹ {sell- (sell-81*sell/100)}</p>
        </div>
      </div>
    </div>
  )
}

export default Dashboard;
