import React, { useEffect, useState } from 'react'
import './Admin.css'
import { NavLink, Outlet, useNavigate } from 'react-router-dom'
import photo from './photo.jpg'

const Admin = () => {

    const navigate = useNavigate();
    const [admin, setadmin] = useState(true);

    const active = ({isActive}) => {
        if(isActive){
            setadmin(false);
        }
    }

    useEffect(()=>{
        if(!localStorage.getItem('admin')){
            navigate('/adminlogin');
        }
    }, [])

    const logoutt = () => {
        localStorage.removeItem('admin');
        navigate('/');
    }

    return (
        <div>
            <div className='admin-panel'>
                <div className='panel'>
                    <div className='logos'>
                        <span className="material-symbols-outlined">
                            store
                        </span>
                        <p className='ecomm'>Ecommerce</p>
                    </div>
                    <NavLink className='logo' to='/admin/dashboard' style={active}>
                        <span className="material-symbols-outlined">
                            home
                        </span>
                        <p className='ecom'>Dashboard</p>
                    </NavLink>
                    <NavLink className='logo' to='/admin/product' style={active}>
                        <span className="material-symbols-outlined">
                            shopping_bag
                        </span>
                        <p className='ecom'>Products</p>
                    </NavLink>
                    <NavLink className='logo' to='/admin/categories' style={active}>
                        <span className="material-symbols-outlined">
                            format_list_bulleted
                        </span>
                        <p className='ecom'>Categories</p>
                    </NavLink>
                    <NavLink className='logo' to='/admin/order' style={active}>
                        <span className="material-symbols-outlined">
                            reorder
                        </span>
                        <p className='ecom'>Orders</p>
                    </NavLink>
                    <NavLink className='logo' to='/admin/admins' style={active}>
                        <span className="material-symbols-outlined">
                            shield_person
                        </span>
                        <p className='ecom'>Admin</p>
                    </NavLink>
                    <button onClick={logoutt} style={{border: 'none', backgroundColor: 'transparent'}} className='logo'>
                        <span className="material-symbols-outlined">
                            logout
                        </span>
                        <p className='ecom'>Logout</p>
                    </button>
                </div>

                <div className='admin'>
                    <div className='navdash'>
                        <div className='hello-name'>
                            <p className='hello'>Hello, <span className='name'>Jatin Mehra</span></p>
                        </div>
                        <div className='navimg'>
                            <div className='imgname'>
                                <img className='img' src={photo} alt='' />
                                <p className='namee'>Jatin Mehra</p>
                            </div>
                        </div>
                    </div>

                    <h1 className='welcome' style={{display: `${admin ? '' : 'none'}`}}>Welcome, Admin</h1>
                    <Outlet />
                </div>
            </div>
        </div>
    )
}

export default Admin;
