import React, { useEffect, useState } from 'react'
import './Admins.css'

const Admins = () => {

  var baseurl = process.env.REACT_APP_URL

  const [email, setemail] = useState();
  const [pass, setpass] = useState();

  const [admin, setadmin] = useState('');

  const add = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/admin`, {
      method: 'post',
      body: JSON.stringify({email, date: new Date(), pass}),
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    if (data.status) {
      alert('SUCCESS: Admin added successfully');
      if (alert) {
        window.location.reload()
      }
    } else {
      alert('ERROR: Admin added failed');
    }
  }

  const admins = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/admin`,{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    setadmin(data);
  }

  useEffect(() => {
    admins();
  }, []);

  const emaildel = async (ele) => {
    let data = await fetch(`https://ecomnode.vercel.app/admin/${ele}`, {
      method: 'delete',
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    if (data.status) {
      alert('SUCCESS: Admin deleted successfully');
      if (alert) {
        window.location.reload()
      }
    } else {
      alert('ERROR: Admin deleted failed');
    }
  }

  return (
    <div>
      <h5>Admins</h5>
      <div className='admin-input'>
        <p className='add-new-admin'>Add new admin</p>
        <input type='text' className='admin-email' value={email} onChange={(e) => setemail(e.target.value)} placeholder='Google email' />
        <input type='password' className='admin-pass' value={pass} onChange={(e) => setpass(e.target.value)} placeholder='Google password' />
        <button className='admin-add' onClick={add}>Add admin</button>
      </div>

      <div>
        <p className='existing-admin'>Existing admins</p>
        <p className='admin-google-email'>ADMINS GOOGLE EMAIL</p>

        <div className='admin-list'>
          {
            admin.length ?
              admin.map((ele, ind) => {
                return (
                  <div className='admin-data'>
                    <p className='admin-email-data'>{ele.email}</p>
                    <p className='admin-pass-data'>{ele.date}</p>
                    <button onClick={() => emaildel(ele._id)} className='admin-del'>
                      <span className="material-symbols-outlined">
                        delete
                      </span>
                      <p style={{marginBottom: '0%'}}>Delete</p>
                    </button>
                  </div>
                )
              }) : null
          }
        </div>
      </div>
    </div>
  )
}

export default Admins;
