import React, { useEffect } from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Nav from './Nav';
import Singup from './Singup';
import Login from './Login';
import Home from './Home';
import Admin from './Admin';
import Dashboard from './Admin-panel/Dashboard';
import Product from './Admin-panel/Product';
import Categories from './Admin-panel/Categories';
import Order from './Admin-panel/Order';
import Admins from './Admin-panel/Admins';
import Add from './Admin-panel/Add';
import Edit from './Admin-panel/Edit';
import Adminlogin from './Adminlogin';
import Allproduct from './Allproduct';
import Blog from './Blog';
import Cart from './Cart';

const App = () => {

  const baseurl = process.env.REACT_APP_URL

  useEffect(async () => {
    if (!localStorage.getItem('auth')) {
      let data = await fetch('https://ecomnode.vercel.app')
      data = await data.json();
      if (data.status) {
        localStorage.setItem('auth', JSON.stringify(data.auth));
      } else {
        alert('API authorization cannot provided. Pls check...')
      }
    }
  }, [])

  return (
    <div>
      <BrowserRouter>
        <Routes>
          <Route path='/' element={<Nav />}>
            <Route index element={<Home />} />
            <Route path='/allproduct' element={<Allproduct />} />
            <Route path='/cart' element={<Cart />} />
            <Route path='/blog' element={<Blog />} />
            <Route path='/adminlogin' element={<Adminlogin />} />
            <Route path='/admin' element={<Admin />}>
              <Route path='/admin/dashboard' element={<Dashboard />} />
              <Route path='/admin/product' element={<Product />} />
              <Route path='/admin/categories' element={<Categories />} />
              <Route path='/admin/order' element={<Order />} />
              <Route path='/admin/admins' element={<Admins />} />
              <Route path='/admin/product/add' element={<Add />} />
              <Route path='/admin/product/edit/:id' element={<Edit />} />
            </Route>
            <Route path='/singup' element={<Singup />} />
            <Route path='/login' element={<Login />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </div >
  )
}

export default App;