import React, { useEffect, useState } from 'react'
import {NavLink} from 'react-router-dom'
import './Product.css'

const Product = () => {

  var baseurl = process.env.REACT_APP_URL
  const [data, setdata] = useState('');

  const result = async () => {
    let data = await fetch('https://ecomnode.vercel.app/product',{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
    }
    })
    data = await data.json();
    setdata(data);
  }

  useEffect(() => {
    result();
  }, [])

  const deleted = async (ele) => {
    let data = await fetch(`https://ecomnode.vercel.app/product/${ele}`,{
      method: 'delete',
      headers: {
          'Content-Type': 'application/json',
          authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    if(data.status){
      alert('Success: Data deleted !!!')
      window.location.reload();
    } else {
      alert('Error: Data cannot deleted !!!')
      window.location.reload();
    }
  }

  return (
    <div>
      <NavLink to='/admin/product/add' className='add'>
        <span className="material-symbols-outlined">
          add
        </span>
        <p className='add-n'>Add new product</p>
      </NavLink>
      <p className='pn'>PRODUCT NAME</p>
      <div className='list'>
        {
          data.length ? data.map((ele, index) => {
            return (
              <div className='product' key={ele._id}>
                <div>
                  <h6>{ele.name}</h6>
                </div>
                <div className='btns'>
                  <NavLink className='edit' to={{
                    pathname: `/admin/product/edit/${ele._id}`
                  }}>
                    <span className="material-symbols-outlined">
                      edit_square
                    </span>
                    <p className='edit-name'>Edit</p>
                  </NavLink>
                  <button onClick={()=>deleted(ele._id)} className='delete'>
                    <span className="material-symbols-outlined">
                      delete
                    </span>
                    <p className='delete-name'>Delete</p>
                  </button>
                </div>
              </div>
            )
          }) : null
        }
      </div>
    </div>
  )
}

export default Product;
