import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import './Add.css'

const Edit = () => {

  var baseurl = process.env.REACT_APP_URL
  const { id } = useParams();
  const navigate = useNavigate();

  const [catelist, setcatelist] = useState('');

  const data = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/product/${id}`, {
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    console.log(data);
    if (data) {
      setname(data.name);
      setcategorie(data.categorie);
      setcolor(data.color);
      setprice(data.price);
      setimg(data.img);
    }
  }

  const list = async () => {
    let data = await fetch('https://ecomnode.vercel.app/categorie',{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    setcatelist(data);
  }
  useEffect(() => {
    data();
    list();
  }, []);

  const [name, setname] = useState('');
  const [categorie, setcategorie] = useState('');
  const [color, setcolor] = useState('');
  const [price, setprice] = useState('');
  const [img, setimg] = useState('');

  const update = async () => {
    let data = await fetch(`https://ecomnode.vercel.app/product/${id}`, {
      method: 'put',
      body: JSON.stringify({ name, categorie, color, price, img }),
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    if (data.status) {
      alert('Success: Data added successfully');
      if (alert) {
        navigate('/admin/product');
      }
    }
    else {
      alert('Error: Data cannot added');
      if (alert) {
        navigate('/admin/product');
      }
    }
  }

  return (
    <div>
      <h4>Edit product</h4>
      <div className='field'>
        <div className='add-item'>
          <p className='item-name'>Product name</p>
          <input className='item-field' type='text' value={name} onChange={(e) => setname(e.target.value)} placeholder='product name' />
        </div>
        <div className='add-item'>
          <p className='item-name'>Categorie</p>
          <select className='item-field' onChange={(e) => setcategorie(e.target.value)}>
            <option></option>
            <optgroup label='<- Categorie-list ->'>
              {
                catelist.length ?
                  catelist.map((ele, ind) => {
                    return (
                      <option key={ele._id} value={ele.cate}>{ele.cate}</option>
                    )
                  }) : null
              }
            </optgroup>
          </select>
        </div>
        <div className='add-item'>
          <p className='item-name'>Color</p>
          <input className='item-field' type='text' value={color} onChange={(e) => setcolor(e.target.value)} placeholder='product color' />
        </div>
        <div className='add-item'>
          <p className='item-name'>Price</p>
          <input className='item-field' type='text' value={price} onChange={(e) => setprice(e.target.value)} placeholder='product price' />
        </div>
        <div className='add-item'>
          <p className='item-name'>Img link</p>
          <input className='item-field' type='text' value={img} onChange={(e) => setimg(e.target.value)} placeholder='product link' />
        </div>
        <button onClick={update} className='sub'>Update</button>
      </div>
    </div>
  )

}

export default Edit;
