import React, { useEffect, useState } from 'react'
import './Order.css'

const Order = () => {

  var baseurl = process.env.REACT_APP_URL
  const [order, setorder] = useState('');

  const item = async () => {
    let data = await fetch('https://ecomnode.vercel.app/order',{
      headers: {
        'Content-Type': 'application/json',
        authorization: JSON.parse(localStorage.getItem('auth'))
    }
    })
    data = await data.json();
    setorder(data);
  }

  useEffect(() => {
    item();
  })

  const deliver = async (ele) => {
    let data = await fetch(`https://ecomnode.vercel.app/order/${ele}`, {
      method: 'delete',
      headers: {
          'Content-Type': 'application/json',
          authorization: JSON.parse(localStorage.getItem('auth'))
      }
    })
    data = await data.json();
    console.log(data);
    if (data.status) {
      alert('<-- Item deliverd -->');
      if (alert) {
        window.location.reload();
      }
    } else {
      window.location.reload();
    }
  }

  return (
    <div>
      <h5>Orders</h5>
      <div className='order'>
        {
          order.length ?
            order.map((ele, ind) => {
              return (
                <div className='order-list' key={ele._id}>
                  <p className='order-name'>{ele.name}</p>
                  <div className='deliverd'>
                    <span class="material-symbols-outlined">
                      orders
                    </span>
                    <button onClick={() => deliver(ele._id)} className='deliver-btn'>Deliverd</button>
                  </div>
                </div>
              )
            }) : null
        }
      </div>
    </div>
  )
}

export default Order
