import React, { useEffect, useState } from 'react'
import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';

const Nav = () => {

    const navigate = useNavigate();
    const auth = localStorage.getItem('user') ? localStorage.getItem('user') : null

    const [orders, setorders] = useState(0);

    const logout = () => {
        localStorage.removeItem('user');
        navigate('/login');
    }
    
    useEffect(()=>{
        if(!auth){
            navigate('/login')
        }
    }, [])

    const userdata = useSelector((data)=>data.Add.user); 
    
    const data = () => {
        setorders(userdata.length)
    }

    useEffect(()=>{
        data();
    }, [userdata])

    return (
        <div>
            <nav className="navbar navbar-expand-lg bg-body-tertiary">
                <div className="container-fluid">
                    <a className="navbar-brand" style={{color: 'orangered', fontWeight: '600', fontSize: '0.6cm'}} href="#">Ecom<span style={{color: 'black'}}>.com</span></a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse align-item-end" id="navbarSupportedContent">
                        <ul className="navbar-nav w-100 me-auto mb-2 mb-lg-0 justify-content-center">
                            <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" aria-current="page" to={auth ? '/' : '/login'}>New product</NavLink>
                            </li>
                            <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" aria-current="page" to={auth ? '/allproduct' : '/login'}>Best sellers</NavLink>
                            </li>
                            <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" aria-current="page" to={auth ? '/cart' : '/login'}>Cart({orders})</NavLink>
                            </li>
                            <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" aria-current="page" to={auth ? '/blog' : '/login'}>Blog</NavLink>
                            </li>
                            <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" to={auth ? '/admin' : '/login'}>Admin</NavLink>
                            </li>
                            {auth ? <li className="nav-item" style={{ marginRight: '50px' }}>
                                <a onClick={logout} className="nav-link" href='#'>Logout</a>
                            </li> : <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" to='/singup'>Sing up</NavLink>
                            </li>}
                            {auth ? null : <li className="nav-item" style={{ marginRight: '50px' }}>
                                <NavLink className="nav-link" to='/login'>Login</NavLink>
                            </li>}
                        </ul>
                    </div>
                </div>
            </nav>

            <Outlet />
        </div>
    )
}

export default Nav;
