import React from 'react'
import './Login.css'
import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';

const Login = () => {

    var baseurl = process.env.REACT_APP_URL
    const navigate = useNavigate();

    const [user, setuser] = useState();
    const [password, setpassword] = useState();

    const login = async () => {
        let data = await fetch('https://ecomnode.vercel.app/user', {
            method: 'put',
            body: JSON.stringify({ user, password }),
            headers: {
                'Content-Type': 'application/json',
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        })
        data = await data.json();
        let status = data.status;
        if (status) {
            localStorage.setItem('user', JSON.stringify(data.data));
            if (localStorage.getItem('user')) {
                navigate('/');
            }
        } else {
            alert('pls fill correct information');
        }
    }

    return (
        <div>
            <div className='login'>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon1">
                        person
                    </span>
                    <input type="text" className="form-control" value={user} onChange={(e) => setuser(e.target.value)} placeholder="Username" aria-label="Username" aria-describedby="basic-addon1" />
                </div>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon2">
                        key
                    </span>
                    <input type="password" className="form-control" value={password} onChange={(e) => setpassword(e.target.value)} placeholder="Password" aria-label="Password" aria-describedby="basic-addon2" />
                </div>
                <button onClick={login} className='btn btn-dark btn-login'>login</button>
                <p className='acc'>Don't have an account then <Link to='/singup'>Singup</Link></p>
            </div>
        </div>
    )
}

export default Login;