import React from 'react'
import './Login.css'
import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';

const Adminlogin = () => {
    var baseurl = process.env.REACT_APP_URL
    const navigate = useNavigate();

    const [user, setuser] = useState('');
    const [password, setpassword] = useState('');

    const adminlogin = async () => {
        let data = await fetch('https://ecomnode.vercel.app/admin', {
            method: 'put',
            body: JSON.stringify({ email:user, pass:password }),
            headers: {
                'Content-Type': 'application/json',
                authorization: JSON.parse(localStorage.getItem('auth'))
            }
        })
        data = await data.json();
        let status = data.status;
        if (status) {
            localStorage.setItem('admin', JSON.stringify(data.data));
            if (localStorage.getItem('admin')) {
                navigate('/admin');
            }
        } else {
            alert('pls fill correct information');
        }
    }

    return (
        <div style={{textAlign: 'center'}}>
            <h4 style={{marginTop: '10px'}}>Admin login Pls</h4>
            <div className='login'>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon1">
                        person
                    </span>
                    <input type="text" className="form-control" value={user} onChange={(e) => setuser(e.target.value)} placeholder="Username" aria-label="Username" aria-describedby="basic-addon1" />
                </div>
                <div className="input-group mb-3">
                    <span class="material-symbols-outlined input-group-text" id="basic-addon2">
                        key
                    </span>
                    <input type="password" className="form-control" value={password} onChange={(e) => setpassword(e.target.value)} placeholder="Password" aria-label="Password" aria-describedby="basic-addon2" />
                </div>
                <button onClick={adminlogin} className='btn btn-dark btn-login'>login</button>
            </div>
        </div>
    )
}

export default Adminlogin;
