import React, { useEffect } from 'react'
import './Home.css'
import Footer from './Footer';
import { adduser, remove } from './redux/slice';
import { useDispatch, useSelector } from 'react-redux';

const Home = () => {

    const dispatch = useDispatch();
    const data = useSelector((data)=>data.Add.user); 
    console.log(data);

    return (
        <div>
            <div>
                <h1 style={{marginTop: '15px'}} className='we-recommended'>Your's orders</h1>
                <div className='homeclient'>
                    {
                        data.length ?
                            data.map((item, ind) => {
                                return (
                                    <div key={ind} className='product-list'>
                                        <img className='product-img' src={item.order.img} alt='img...' />
                                        <h3 className='product-names'>{item.order.name}</h3>
                                        <span className='product-price'>€{item.order.price}</span>
                                        {
                                            <div className='mobile-size-color'>
                                                <select className='product-size'>
                                                    <option>40 x 60</option>
                                                    <option>80 x 120</option>
                                                </select>
                                            </div>
                                        }
                                        <input onClick={()=>dispatch(remove(item.id))} className='stock' type='button' value='Remove product' style={{cursor: 'pointer'}} />
                                    </div>
                                )
                            }) : <p className='we-recommended' style={{fontWeight: 'bold'}}>You don't Order anything !!!</p>
                    }
                </div>
            </div>

            <Footer />
        </div>
    )
}

export default Home;
